.gigwell-branding {
  display: none;
}

.gigwell-roster {
    height: calc(100vh - 140px);
    width: 100vw;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    margin: 0 none;
    padding: 0 none;
}

.gigwell-roster-artist-title {
  font-size: 20px;
  -webkit-transition: color 0.3s;
  -o-transition: color 0.3s;
  transition: color 0.3s;
}

.gigwell-roster-artist-title:hover {
  color: tomato;
}

#gigwell-embedded-roster a.btn-link.gigwell-profile-back {
  text-transform: uppercase;
}


