.menu-toggle {
  display: none;
}

.menu-link {
  color: white;
  background: none;
  border: 0px none;
  cursor: pointer;
  font-size: 14px;
  letter-spacing: 2px;
  padding: 10px 0;
  margin: 10px;
  overflow: hidden;
  position: relative;
  text-decoration: none;
  text-transform: uppercase;
}

.menu-link:focus {
  outline: none;
}

.menu-link-selected:after,
.menu-link:hover:after {
  -webkit-transform-origin: center;
      -ms-transform-origin: center;
          transform-origin: center;
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: 100;
  content: '';
  height: 1px;
  width: 100%;
  background-color: rgba(255,255,255,0.5);
  -webkit-animation: grow-in 0.3s ease;
          animation: grow-in 0.3s ease;
}

/* Mobile View */

@media (max-width : 768px) {
  .menu-toggle {
    display: block;
    background: none;
    border: none;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-direction: column;
        flex-direction: column;
    cursor: pointer;
  }

  .menu-toggle:focus {
    outline: none;
  }

  .menu-toggle > .toggle-line {
    width: 20px;
    margin-top: 4px;
    height: 2px;
    background: white;
    -webkit-transition: -webkit-transform 0.5s cubic-bezier(
      0.680, 
      -0.550, 
      0.265, 
      1.550
    );
    transition: -webkit-transform 0.5s cubic-bezier(
      0.680, 
      -0.550, 
      0.265, 
      1.550
    );
    -o-transition: transform 0.5s cubic-bezier(
      0.680, 
      -0.550, 
      0.265, 
      1.550
    );
    transition: transform 0.5s cubic-bezier(
      0.680, 
      -0.550, 
      0.265, 
      1.550
    );
    transition: transform 0.5s cubic-bezier(
      0.680, 
      -0.550, 
      0.265, 
      1.550
    ), -webkit-transform 0.5s cubic-bezier(
      0.680, 
      -0.550, 
      0.265, 
      1.550
    );
  }

  .menu-toggle-active {
    margin-top: 0px;
  }

  .menu-toggle-active > .toggle-line:nth-child(1) {
    -webkit-transform: scaleX(1.2);
        -ms-transform: scaleX(1.2);
            transform: scaleX(1.2);
  }

  .menu-toggle-active > .toggle-line:nth-child(2) {
    -webkit-transform: scaleX(0.6);
        -ms-transform: scaleX(0.6);
            transform: scaleX(0.6);
  }

  .menu-toggle-active > .toggle-line:nth-child(3) {
    -webkit-transform: scaleX(01.2);
        -ms-transform: scaleX(01.2);
            transform: scaleX(01.2);
  }

  .menu {
    position: absolute;
    left: 0;
    top: 0;
    -webkit-transform: translateX(-200%);
        -ms-transform: translateX(-200%);
            transform: translateX(-200%);
    height: 100vh;
    width: calc(100vw - 70px);
    background: white;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-direction: column;
        flex-direction: column;
    z-index: 100;
    -webkit-animation: slide-in 0.3s ease-out;
            animation: slide-in 0.3s ease-out;
    -webkit-box-shadow: 0 0 40px 10px rgba(0,0,0,.3);
            box-shadow: 0 0 40px 10px rgba(0,0,0,.3);
  }

  .menu-active {
    -webkit-transform: translateX(0%);
        -ms-transform: translateX(0%);
            transform: translateX(0%);
    -webkit-animation: slide-out 0.3s ease-out;
            animation: slide-out 0.3s ease-out;

  }

  .menu-active > .menu-link {
    -webkit-animation: slide-out 1s ease;
            animation: slide-out 1s ease;
  }

  .menu-link {
    color: rgba(0,0,0,0.3);
  }

  .menu-link:hover:after,
  .menu-link-selected:after {
    background-color: tomato;
  }
}

@-webkit-keyframes slide-out {
  0% {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
  }
  100% {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
  }
}

@keyframes slide-out {
  0% {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
  }
  100% {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
  }
}

@-webkit-keyframes slide-in {
  0% {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
  }
  100% {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
  }
}

@keyframes slide-in {
  0% {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
  }
  100% {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
  }
}

@-webkit-keyframes grow-in {
  0% { 
    opacity: 0;
    -webkit-transform: scaleX(0);
            transform: scaleX(0); 
  }
  100% { 
    opacity: 1;
    -webkit-transform: scaleX(80%);
            transform: scaleX(80%); 
  }
}

@keyframes grow-in {
  0% { 
    opacity: 0;
    -webkit-transform: scaleX(0);
            transform: scaleX(0); 
  }
  100% { 
    opacity: 1;
    -webkit-transform: scaleX(80%);
            transform: scaleX(80%); 
  }
}




